:root {
  --pri-font:"Kumbh Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);
  --v-dark-blue: hsl(220, 13%, 13%);
  --dark-g-blue: hsl(219, 9%, 45%);
  --g-blue: hsl(220, 14%, 75%);
  --light-g-blue: hsl(223, 64%, 98%);
  --light-black: hsl(0, 0%, 75%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
  overflow-x: hidden;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none !important;
  }
}

.no-scroll {
  overflow: hidden !important;
}

.overlay {
  background-color: var(--black);
  opacity: 0.86;
  z-index: 95;
  transition: all 0.2s ease-in-out;
}

.nav__linksCtn--header {
  cursor: pointer;
}
.nav__linksCtn ul li {
  color: var(--g-blue);
  position: relative !important;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.nav__linksCtn ul li::before {
  content: "";
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2.9rem;
  height: 4px;
  background-color: var(--orange);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.nav__linksCtn ul li:hover {
  color: var(--black);
}
.nav__linksCtn ul li:hover::before {
  opacity: 1;
}
.nav__linksCtn #mobileNav {
  outline: 1px solid blue;
  width: 60%;
  left: -100%;
  text-align: left;
  z-index: 99;
  transition: all 0.2s ease-in-out;
}
.nav__linksCtn #mobileNav img {
  align-self: flex-start;
  margin-bottom: 1rem;
}
.nav__linksCtn #mobileNav li {
  color: var(--black) !important;
  font-weight: 600;
  align-self: flex-start;
}
.nav__linksCtn #mobileNav li::before {
  display: none !important;
}
.nav__linksCtn #mobileNav.active {
  left: 0;
}
.nav__cartAndUserCtn #cartIcon {
  cursor: pointer;
  opacity: 0.75;
  transition: all 0.2s ease-in-out;
}
.nav__cartAndUserCtn #cartIcon:hover {
  opacity: 1;
}
.nav__cartAndUserCtn .cartQuantity {
  background-color: var(--orange);
}
.nav__cartAndUserCtn #avatar {
  cursor: pointer;
  border-radius: 100%;
  border: none;
  outline: 2px solid var(--white);
  transition: all 0.2s ease-in-out;
}
.nav__cartAndUserCtn #avatar:hover {
  outline: 2px solid var(--orange) !important;
}

button {
  background-color: var(--orange) !important;
  transition: all 0.2s ease-in-out;
}
button:hover {
  opacity: 0.8;
}

.cartModal {
  width: 95%;
  max-width: 22.5rem;
}
@media (max-width: 39.937em) {
  .cartModal {
    left: 0;
    right: 0 !important;
    margin: auto;
  }
}

main .modal {
  z-index: 96;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
}
main .modal__topImg img {
  width: 62vh;
}
main .modal .closeCtn svg * {
  transition: all 0.2s ease-in-out;
}
main .modal .closeCtn svg:hover * {
  fill: var(--orange);
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn {
  position: relative !important;
  cursor: pointer;
  border-radius: 1rem;
  transition: all 0.2s ease-in-out;
  border: 2px solid hsla(0deg, 0%, 100%, 0);
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn::before {
  content: "";
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  border-radius: 1rem;
  transition: all 0.3s ease-in-out;
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn.active {
  border: 2px solid var(--orange);
  pointer-events: none;
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn.active::before {
  opacity: 0.5;
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn.active:hover {
  opacity: 1;
}
main .modal .modalThumbnailsCtn .modalThumImg__ctn:hover {
  opacity: 0.7;
}
main .modal .modalThumbnailsCtn img {
  transition: all 0.2s ease-in-out;
  width: 4rem;
  cursor: pointer;
}
main .modal .modalBtn {
  top: 40%;
  cursor: pointer;
}
main .modal .modalBtn:hover svg {
  transition: all 0.2s ease-in-out;
}
main .modal .modalBtn:hover svg * {
  transition: all 0.2s ease-in-out;
}
main .modal .modalBtn:hover svg * {
  stroke: var(--orange);
}
main .imgCtn__mobileSlide {
  height: 47vh;
}
main .imgCtn__mobileSlide .slideBtn {
  top: 45%;
}
main .imgCtn__desktopCtn .topImgCtn img {
  width: 86% !important;
  height: 70vh;
  object-fit: cover;
  cursor: pointer;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn {
  position: relative !important;
  cursor: pointer;
  border-radius: 1rem;
  transition: all 0.2s ease-in-out;
  border: 2px solid #fff;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn::before {
  content: "";
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  border-radius: 1rem;
  transition: all 0.3s ease-in-out;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn.active {
  border: 2px solid var(--orange);
  pointer-events: none;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn.active::before {
  opacity: 0.5;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn.active:hover {
  opacity: 1;
}
main .imgCtn__desktopCtn .thumbnailsCtn .thumImg__ctn:hover {
  opacity: 0.7;
}
main .imgCtn__desktopCtn .thumbnailsCtn img {
  transition: all 0.2s ease-in-out;
  width: 5.5rem;
}
main .productDetails__subHeader {
  color: var(--orange);
  text-transform: uppercase;
}
main .productDetails__priceCtn .discount {
  color: var(--orange);
  background-color: var(--pale-orange);
}
main .productDetails__priceCtn .oldPrice {
  color: var(--dark-g-blue);
  opacity: 0.6 !important;
}
main .productDetails__quantityCtn {
  background-color: var(--light-g-blue);
}
main .productDetails__quantityCtn img {
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
main .productDetails__quantityCtn img:hover {
  opacity: 0.7;
}
main .productDetails button {
  background-color: var(--orange);
  transition: all 0.2s ease-in-out;
}
main .productDetails button:hover {
  opacity: 0.7;
}

